<template>
    <div class="shops-nav" ref="test">
        <!-- //* label -->
        <nav class="shops-nav-label">
            <span class="shops-nav-label-item"
                @click="tabPopup(index)"
                v-for="(item, index) in shopsNavs" :key="index"
            >
                <span>{{item.label}}</span>
                <i class="iconfont"
                    :class="{'icon-down': !item.popupState, 'icon-up': item.popupState, 'icon-active': item.popupState}"></i>
            </span>
        </nav>
        <!-- //* popup -->
        <transition
            @enter="slideDown"
            @leave="slideUp"
            @enter-cancelled="cancelSlideDown"
            :css="false"
        >
            <section class="shops-nav-popup"
                v-if="popup"
                @click="closePopup"
            >
                <div class="shops-nav-popup-inner">
                    <!-- //* 全部美食 -->
                    <div class="shops-nav-all_section"
                        v-if="shopsNavs[0].popupState"
                    >
                        <ul class="popup_list">
                            <li class="popup_list-item"
                                :class="{'popup_list-item--active': index === allFoodsIndex}"
                                v-for="(label, index) in allFoods" :key="index"
                                @click="filterFoods(index)"
                            >
                                {{label}}
                            </li>
                        </ul>
                    </div>
                    <!-- //* 全城 -->
                    <div class="shops-nav-all_section"
                        v-if="shopsNavs[1].popupState"
                    >
                        <ul class="popup_list">
                            <li class="popup_list-item"
                                :class="{'popup_list-item--active': index === areasIndex}"
                                v-for="(label, index) in areas" :key="index"
                                @click="filterAreas(index)"
                            >
                                {{label}}
                            </li>
                        </ul>
                    </div>
                    <!-- //* 智能排序 -->
                    <div class="shops-nav-all_section"
                        v-if="shopsNavs[2].popupState"
                    >
                        <ul class="popup_list">
                            <li class="popup_list-item"
                                :class="{'popup_list-item--active': index === intelligentSortsIndex}"
                                v-for="(label, index) in intelligentSorts" :key="index"
                                @click="filterIntelligences(index)"
                            >
                                {{label}}
                            </li>
                        </ul>
                    </div>
                    <!-- //* 全部筛选 -->
                    <div class="shops-nav-all_section"
                        v-if="shopsNavs[3].popupState"
                    >
                        <ul class="popup_list">
                            <li class="popup_list-item"
                                :class="{'popup_list-item--active': index === filtersIndex}"
                                v-for="(label, index) in filters" :key="index"
                                @click="filterFilters(index)"
                            >
                                {{label}}
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        </transition>
    </div>
</template>

<script>
import velocity from 'velocity-animate'
import { mapMutations } from 'vuex'

export default {
    name: 'ShopsNav',
    data() { 
        return {
            shopsNavs: [
                {
                    label: '全部美食',
                    popupState: false
                },
                {
                    label: '全城',
                    popupState: false
                },
                {
                    label: '智能排序',
                    popupState: false
                },
                {
                    label: '全部筛选',
                    popupState: false
                },
            ],
            allFoods: ['全部美食', '食品保健', '特色菜', '福建菜', '日本菜', '生日蛋糕'],
            allFoodsIndex: 0,
            areasIndex: 0,
            areas: ['全城', '白云区', '越秀区', '番禺区', '海珠区', '天河区'],
            intelligentSorts: ['智能排序', '离我最近', '好评优先', '销量最高'],
            intelligentSortsIndex: 0,
            filters: ['全部筛选', '单人餐', '双人餐', '外卖送餐','订座'],
            filtersIndex: 0,
        }
    },
    computed: {
        popup(){
            return this.shopsNavs.some(item => item.popupState === true)
        },
    },
    methods: {
        ...mapMutations('shops', [
            'setType',
            'setLocation',
            'setService',
            'setSorttype'
        ]),
        // 切换弹窗
        tabPopup(index){
            this.shopsNavs.map((item, itemIndex) => {
                if(index === itemIndex){
                    // this.$set()
                    item.popupState = !item.popupState
                }else{
                    item.popupState = false
                }
            })
        },

        // 弹窗动画
        slideDown(el, done){
            velocity(el, 'slideDown', { duration: 300 }, {complete: done})
        },
        slideUp(el, done){
            velocity(el, 'slideUp', { duration: 300 }, {complete: done})
        },
        cancelSlideDown(el){
            velocity(el, 'stop')
        },

        // 关闭弹窗
        closePopup(){
            this.shopsNavs.map(item => item.popupState = false)
        },

        // 全部筛选
        filterFoods(index){
            this.allFoodsIndex = index
            this.shopsNavs[0].label = this.allFoods[index]
            this.setType(this.allFoods[index])
            // this.closePopup()
        },
        filterAreas(index){
            this.areasIndex = index
            this.shopsNavs[1].label = this.areas[index]
            this.setLocation(this.areas[index])
            // this.closePopup()
        },
        filterIntelligences(index){
            this.intelligentSortsIndex = index
            this.shopsNavs[2].label = this.intelligentSorts[index]
            this.setSorttype(this.intelligentSorts[index])
            // this.closePopup()
        },
        filterFilters(index){
            this.filtersIndex = index
            this.shopsNavs[3].label = this.filters[index]
            this.setService(this.filters[index])
            // this.closePopup()
        },
    },
    mounted() {
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.shops-nav{
    // * label
    &-label{ // .shops-nav-label
        .flex();
        .justify-content(space-between);
        .padding-horizontal(@padding-base);
        background-color: @bg-white;
        &-item{ // .shops-nav-label-item
            .flex-basis(100px);
            .flex-grow(1);
            .padding-vertical(@padding-base);
            .font-size-rem(@font-size-sm);
            .flex-center();
            color:#000;
            .icon-active{
                color: @color-primary;
            }
        }
    }
    // * popup
    &-popup{ // .shops-nav-popup
        // position: fixed;
        // height: 100%;
        // background-color: @bg-popup;
        // width: 100%;
        .popup();
        &-inner{
            // background-color: @bg-white;
            // position: absolute;
            // width: 100%;
            // left: 0;
            // top: 0;
            .padding-horizontal(20px);
        }
        .popup_list{
            &-item{ // .popup_list-item
                .padding-vertical(12px);
                font-size: 12px;
                color: #000;
                &--active{ // .popup_list-item--active
                    color: @color-primary;
                }
            }
        }
    }
}
</style>