<template>
    <Layout closeTabbar>
        <template v-slot:header>
            <!-- header -->
            <section class="shops-header">
                <i class="iconfont icon-back-m shops-header-back" @click="$router.go(-1)"></i>
                <!-- //* 搜索框 -->
                <el-input class="search-input"
                    v-model="input"
                    placeholder="输入商家名、品类或地区"
                    prefix-icon="el-icon-search"
                    size="small"></el-input>
                <span class="none"></span>
            </section>
            <!-- nav 导航 -->
            <ShopsNav/>
        </template>
        <!-- //* 商家列表 -->
        <Shopslist/>
    </Layout>
</template>

<script>
import Layout from '../layout/Layout.vue'
import ShopsNav from './ShopsNav.vue'
import Shopslist from './Shopslist.vue'
import { mapState, mapGetters } from 'vuex'
export default {
    name: 'Shop',
    components: {
        Layout,
        ShopsNav,
        Shopslist
    },
    data() { 
        return {
            input: ''
        }
    },
    computed: {
        ...mapState('shops', {
            shops: state => state.shops
        }),
        ...mapGetters('shops', [
            'filterShops'
        ]),
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.shops-header{
    .flex-sb-center();
    background-color: @bg-white;
    .padding-vertical(@padding-base);
    &-back,
    .none{
        .flex-basis(28px);
        .flex-shrink(0);
    }
    &-back{
        text-align: center;
    }
    .none{
        visibility: hidden;
    }
}
</style>